import {Component, View, CORE_DIRECTIVES, FORM_DIRECTIVES} from 'angular2/angular2';
import {Input, Output, ElementRef, ViewChildren, QueryList} from 'angular2/angular2';
import {DatePickerCalendar} from './DatePickerCalendar';
import {Animation} from '../../Directives/Animation/Animation'

@Component({
	selector: 'date-picker'
})
@View({
	styles: [`
   .input-group-addon {
     background-color: #fff;
     border-left: none; }

   .modal {
     display: block;
     -webkit-transition: all 0.1s ease;
     -moz-transition: all 0.1s ease;
     transition: all 0.1s ease; }

   .modal.ng-enter {
     opacity: 0;
     -webkit-transform: rotateX(-90deg);
     -moz-transform: rotateX(-90deg);
     -ms-transform: rotateX(-90deg);
     -o-transform: rotateX(-90deg);
     transform: rotateX(-90deg); }

   .modal.ng-enter-active {
     opacity: 1;
     -webkit-transform: rotateX(0deg);
     -moz-transform: rotateX(0deg);
     -ms-transform: rotateX(0deg);
     -o-transform: rotateX(0deg);
     transform: rotateX(0deg); }

   .modal.ng-leave {
     opacity: 1;
     -webkit-transform: rotateX(0deg);
     -moz-transform: rotateX(0deg);
     -ms-transform: rotateX(0deg);
     -o-transform: rotateX(0deg);
     transform: rotateX(0deg); }

   .modal.ng-leave-active {
     opacity: 0;
     -webkit-transform: rotateX(90deg);
     -moz-transform: rotateX(90deg);
     -ms-transform: rotateX(90deg);
     -o-transform: rotateX(90deg);
     transform: rotateX(90deg); }

   .modal-dialog {
     display: inline-block;
     width: 400px;
     height: 300px;
     margin: 0;
     position: relative; }

   .calendar-container {
     overflow: hidden;
     border: 1px solid transparent;
     white-space: nowrap; }

   date-picker-calendar {
     padding-top: .5rem !important; }

   date-picker-calendar.left.enter {
     -webkit-animation: slideInLeft 0.2s ease;
     -moz-animation: slideInLeft 0.2s ease;
     animation: slideInLeft 0.2s ease; }

   date-picker-calendar.left.leave {
     margin-right: -100%;
     -webkit-animation: slideOutLeft 0.2s ease;
     -moz-animation: slideOutLeft 0.2s ease;
     animation: slideOutLeft 0.2s ease; }

   date-picker-calendar.right.enter {
     -webkit-animation: slideInRight 0.2s ease;
     -moz-animation: slideInRight 0.2s ease;
     animation: slideInRight 0.2s ease; }

   date-picker-calendar.right.leave {
     margin-left: -50%;
     -webkit-animation: slideOutRight 0.2s ease;
     -moz-animation: slideOutRight 0.2s ease;
     animation: slideOutRight 0.2s ease; }

   .input-group-addon {
     background-color: #fff;
     border-left: none; }

   header {
     position: relative;
     top: 0;
     left: 0; }

   .prev-month, .next-month {
     position: absolute;
     top: 0;
     display: inline-block;
     z-index: 100;
     margin-top: .2rem; }
     .prev-month .btn-sm, .next-month .btn-sm {
       padding: .1rem .7rem; }

   .prev-month {
     left: 0;
     margin-left: 4%; }

   .next-month {
     right: 0;
     margin-right: 4%; }
	`],
	template: `
   <div class="input-group" (click)="showCalendar($event)">
   	<input type="text" class="form-control"
   		[(ng-model)]="inputDate" 
   		 #date-field
   		 />
   	<span class="input-group-addon" [class.input-group-addon-focus]="dateField.focus">
   		<i class="fa fa-calendar"></i>
   	</span>
   </div>

   <section class="modal ng-animate" *ng-if="calendarDisplayed">
   <div class="modal-dialog" role="document"
   	[style.top.px]="calendarY"
   	[style.left.px]="calendarX">
   <div class="modal-content container p-a-0">
   	<header class="row">
   		<div class="prev-month">
   			<button class="btn btn-primary btn-sm" role="prev"
   				[class.disabled]="!canPrevMonth()"			 
   				(click)="prevMonth()">
   				<i class="fa fa-chevron-circle-left"></i>
   			</button>
   		</div>
   		<div class="next-month">
   			<button class="btn btn-primary btn-sm" role="next"
   				[class.disabled]="!canNextMonth()"
   				(click)="nextMonth()">
   				<i class="fa fa-chevron-circle-right"></i>
   			</button>
   		</div>
   	</header>
   	<section class="calendar-container">
   		<date-picker-calendar animation
   			*ng-for="#month of calendarMonths #i=index"
   			class="col-md-{{12/months}} p-a-0"
   			[style.margin-left]="(i!=months||direction!='right'?0:-100/months)+'%'" 
   			[id]="i"
   			[min-date]="minDate" [max-date]="maxDate"
   			[current-month]="month" 
   			[(selected-date)]="selectedDate" 
   			(selected-date)="hideCalendar()"
   			 />
   	</section>
   </div>
   </div>
   </section>
	`,
	directives: [DatePickerCalendar, CORE_DIRECTIVES, FORM_DIRECTIVES, Animation]
})
export class DatePickerBase {

	_minDate: Date = new Date(1900,0,1);
	_maxDate: Date = new Date(2200,0,1);

	get minDate(): Date|string { return this._minDate; };
	set minDate(value: Date|string) {
		this._minDate = this.handleDateInput(value);
	}
	
	get maxDate(): Date|string { return this._maxDate; }
	set maxDate(value: Date|string) {
		this._maxDate = this.handleDateInput(value);
	}

	months: number = 1;
	calendarMonths: Date[] = [];

	calendarDisplayed: boolean = true;

	calendarX: number = 1;
	calendarY: number = 1;
	
	direction: string = "";
	isAnimating: boolean = false;

	currentDate: Date;
	modal: HTMLElement;
	
	calendarQuery: QueryList<Animation>;

	constructor(modal: ElementRef) {
		this.modal = modal.nativeElement;
	}
	
	onInit(): void {
		this.calendarMonths = [];
		for(let i = 0; i < this.months; i++)
			this.calendarMonths
				.push(new Date(this.currentDate.getFullYear(), this.currentDate.getMonth()+i));
		this.currentDate = this.currentDate;
	}

	afterViewInit(): void {			
		this.modal.addEventListener('click', (e) => {
			if(e.srcElement.className.indexOf('modal') != -1)
				this.hideCalendar();
		});
		
		(<any>this.calendarQuery.changes).toRx()
			.subscribe((calendars: any) => this.updateCalendars(calendars));
	}

	handleDateInput(value: string|Date): Date {
		if(value instanceof Date && !isNaN(value.valueOf()))
			return value;
		else
			return new Date(<string>value);
	}	
	
	showCalendar(event: MouseEvent): void {
		
		if(event != null) {
			var clickedRect = event.srcElement.parentElement.getBoundingClientRect();
			this.calendarX = clickedRect.left;
			if(screen.height - clickedRect.bottom <= 400) {
				this.calendarY = (clickedRect.top - 290 + clickedRect.height);
			} else {
				this.calendarY = (clickedRect.top + clickedRect.height);
			}
		}			
		
		this.onInit();
		this.calendarDisplayed = true;
		this.direction = '';
	}
	
	hideCalendar(): void {
		this.calendarDisplayed = false;
		this.direction = '';
	}
	
	canPrevMonth(): boolean {
		var prevDate = 
			new Date(this.currentDate.getFullYear(), this.currentDate.getMonth()-1);
		var compareDate = 
			new Date(this._minDate.getFullYear(), this._minDate.getMonth());
		return prevDate >= compareDate;
	}
	
	prevMonth(): void {
		if(!this.canPrevMonth() || this.isAnimating)
			return;
			
		var firstMonth = this.calendarMonths[0];
		this.calendarMonths.unshift(
			new Date(firstMonth.getFullYear(), firstMonth.getMonth()-1)
		); 
		this.direction = 'right';
	}
	
	canNextMonth(): boolean {
		var nextDate = 
			new Date(this.currentDate.getFullYear(), this.currentDate.getMonth()+1); 
		var compareDate = 
			new Date(this._maxDate.getFullYear(), this._maxDate.getMonth()-1);
		return nextDate <= compareDate;
	}
	
	nextMonth(): void {
		if(!this.canNextMonth() || this.isAnimating)
			return;
			
		var lastMonth = this.calendarMonths[this.calendarMonths.length-1];
		this.calendarMonths.push(
			new Date(lastMonth.getFullYear(), lastMonth.getMonth()+1)
		);
		this.direction = 'left';	
	}
	
	updateCalendars(calendars: QueryList<Animation>): void {
		if(this.direction.length == 0 || this.isAnimating)
			return;
		
		this.isAnimating = true;
		var direction = this.direction;	
		
		var cleanAction: () => void;
		
		if(direction == 'right') {
			calendars.first
				.addAnimation(direction+'.enter')
				.startAnimation(() => {
						this.direction = ''
						this.isAnimating = false;
						this.calendarMonths.pop()
					});
			calendars.last
				.addAnimation(direction+'.leave')
				.startAnimation();			
		} else {
			calendars.first
				.addAnimation(direction+'.leave')
				.startAnimation();
			calendars.last
				.addAnimation(direction+'.enter')
				.startAnimation(() => {
						this.direction = ''
						this.isAnimating = false;
						this.calendarMonths.shift()
					});
		}	

		calendars
			.filter((c: Animation) => 
				c !== calendars.first && c !== calendars.last)
			.map((c: Animation) => {
				c.addAnimation(direction+'.enter')
					.startAnimation();
			});		
	}
}